import { useState, useEffect  } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import { Form, Button, Row, Col } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import FormContainer from '../components/FormContainer'
import Loader from '../components/Loader'
import { useRegisterMutation } from '../slices/usersApiSlice'
import { setCredentials } from '../slices/authSlice'
import { toast } from 'react-toastify'

const RegisterScreen = () => {
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')

  const dispatch = useDispatch(); 
  const navigate = useNavigate(); 

  //note: isLoading is created autmatically for us
  const [register, {isLoading}] = useRegisterMutation(); 
 
  const { userInfo } = useSelector((state) => state.auth); 

  const { search } = useLocation(); 
  const sp = new URLSearchParams(search);
  const redirect = sp.get('redirect') || '/';

  //check to see if we are logged in
  useEffect(() => {
    if (userInfo) {
      navigate(redirect);
    } 
  }, [userInfo, redirect, navigate]);

  //we login by calling login from useApiSlice and setCredentials from our authSlice
  const submitHandler = async (e) => {
    e.preventDefault();
    // check is password matches 
    if (password !== confirmPassword) {
      toast.error('Passwords do not match');
      return; 
    } else {
      try {
        const res = await register({name, email, password }).unwrap(); 
        dispatch(setCredentials({...res, }));
        navigate(redirect); 
      } catch (err) {
        toast.error(err?.data?.message || err.error);
      }
    }
  }
  
  return (
  <FormContainer>
    <h1>Sign Up</h1>
    <Form onSubmit={submitHandler}>
    <Form.Group controlId='name' className='my-3'>
        <Form.Label>Name</Form.Label>
        <Form.Control type='name' placeholder='Enter name' value={name} onChange={(e) => setName(e.target.value)}>
        </Form.Control>
      </Form.Group>
      <Form.Group controlId='email' className='my-3'>
        <Form.Label>Email Address</Form.Label>
        <Form.Control type='email' placeholder='Enter email' value={email} onChange={(e) => setEmail(e.target.value)}>
        </Form.Control>
      </Form.Group>
      <Form.Group controlId='password' className='my-3'>
        <Form.Label>Password</Form.Label>
        <Form.Control type='password' placeholder='Enter password' value={password} onChange={(e) => setPassword(e.target.value)}>
        </Form.Control>
      </Form.Group>
      <Form.Group controlId='confirmPassword' className='my-3'>
        <Form.Label>Confirm password</Form.Label>
        <Form.Control type='confirmPassword' placeholder='Confirm password' value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)}>
        </Form.Control>
      </Form.Group>
      <Button type='submit' variant='primary' className='my-3' disabled={ isLoading }>
        Register
      </Button>
      { isLoading && <Loader />}
    </Form>
    <Row className='py-3'>
      <Col>
        Already have an account? <Link to={redirect ? `/login?redirect=${redirect}`: '/login' }>Login</Link>
      </Col>
    </Row>
  </FormContainer>
  )
}

export default RegisterScreen