import { useState } from 'react'
import { Form, Button } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import FormContainer from '../components/FormContainer';
import { saveShippingAddress } from '../slices/cartSlice';
import CheckoutSteps from '../components/CheckoutSteps';

// dev note - we want to show the screen so we can create the route, but then we wouldn't want to keep it that way because we need to create a private route! We don't want just anyone being able to access this screen. 

const ShippingScreen = () => {
  const cart = useSelector((state) => state.cart);
  const { shippingAddress } = cart; 

  const [address, setAddress] = useState(shippingAddress?.address || '');
  const [city, setCity] = useState(shippingAddress?.city || '');
  const [postalCode, setPostalCode] = useState(shippingAddress?.postalCode || '');
  const [country, setCountry] = useState(shippingAddress?.country || '');

  const navigate = useNavigate(); 
  const dispatch = useDispatch();
  
  const submitHandler = (e) => {
    e.preventDefault(); 
    dispatch(saveShippingAddress({address, city, postalCode, country}))
    navigate('/payment')
  }

  return (
    <FormContainer>
    <CheckoutSteps step1 step2 />
      <h1>Shipping</h1>
      <Form onSubmit={submitHandler}>
        <Form.Group controlId='address' className='my-2'>
          <Form.Label>Address</Form.Label>
          <Form.Control
          type='text'
          placeholder='Enter address'
          value={address}
          onChange={(e) => setAddress(e.target.value)}>
          </Form.Control>
        </Form.Group>
        <Form.Group controlId='address' className='my-2'>
          <Form.Label>City</Form.Label>
          <Form.Control
          type='text'
          placeholder='Enter city'
          value={city}
          onChange={(e) => setCity(e.target.value)}>
          </Form.Control>
        </Form.Group>
        <Form.Group controlId='postalCode' className='my-2'>
          <Form.Label>Postal code</Form.Label>
          <Form.Control
          type='text'
          placeholder='Enter postal code'
          value={postalCode}
          onChange={(e) => setPostalCode(e.target.value)}>
          </Form.Control>
        </Form.Group>
        <Form.Group controlId='postalCode' className='my-2'>
          <Form.Label>Country</Form.Label>
          <Form.Control
          type='text'
          placeholder='Enter country'
          value={country}
          onChange={(e) => setCountry(e.target.value)}>
          </Form.Control>
        </Form.Group>
        <Button type='submit' variant='primary' className='my-2'>
          Continue
        </Button>
      </Form>
    </FormContainer>
  )
}

export default ShippingScreen